/* Custom Tennis Themed Color Variables */
:root {
  /* Tennis Ball Green Theme */
  --tennis-green: #c8e53f;
  --tennis-green-dark: #a6c32e;
  --tennis-green-light: #d9f05c;
  
  /* Court Colors */
  --court-blue: #2563eb;
  --court-blue-dark: #1d4ed8;
  --court-blue-light: #3b82f6;
  
  /* Neutral Colors */
  --white: #ffffff;
  --gray-50: #f9fafb;
  --gray-100: #f3f4f6;
  --gray-200: #e5e7eb;
  --gray-300: #d1d5db;
  --gray-400: #9ca3af;
  --gray-500: #6b7280;
  --gray-600: #4b5563;
  --gray-700: #374151;
  --gray-800: #1f2937;
  --gray-900: #111827;
  
  /* Status Colors */
  --success: #10b981;
  --warning: #f59e0b;
  --error: #ef4444;
  
  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
  --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}

/* Basic Reset */
* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: normal normal normal 1rem/1.6 -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  color: var(--gray-700);
  background: #f1f2e7;
  font-size: 1rem;
  min-height: 100vh;
}

/* Containers */
.container {
  max-width: 1200px;
  padding: 0 1rem;
  margin-left: auto;
  margin-right: auto;
}

.small-container {
  max-width: 800px;
  padding: 0 1rem;
  margin-left: auto;
  margin-right: auto;
}

/* Buttons */
.button,
a.button,
button,
[type="submit"],
[type="reset"],
[type="button"] {
  -webkit-appearance: none;
  display: inline-block;
  border: 1px solid var(--tennis-green);
  border-radius: 4px;
  background: var(--tennis-green);
  color: var(--gray-900);
  font-weight: 600;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1rem;
  text-transform: none;
  padding: 0.75rem 1.25rem;
  margin: 0 0 0.5rem 0;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  line-height: 1;
}

.button:hover,
a.button:hover,
button:hover,
[type="submit"]:hover,
[type="reset"]:hover,
[type="button"]:hover {
  border: 1px solid var(--tennis-green-dark);
  background: var(--tennis-green-dark);
  color: #ffffff;
  text-decoration: none;
}

.button:focus,
.button:active,
a.button:focus,
a.button:active,
button:focus,
button:active,
[type="submit"]:focus,
[type="submit"]:active,
[type="reset"]:focus,
[type="reset"]:active,
[type="button"]:focus,
[type="button"]:active {
  border: 1px solid var(--tennis-green-dark);
  background: var(--tennis-green-dark);
  outline: 0;
}

.cancel-button,
a.cancel-button {
  background: transparent;
  border: 1px solid var(--error);
  color: var(--error);
}

.cancel-button:hover,
a.cancel-button:hover {
  color: #ffffff;
  border: 1px solid #c53030;
  background: #c53030;
}

.neutral-button:hover,
a.neutral-button:hover {
  color: #ffffff;
  border: 1px solid #8fa325;
  background: #8fa325;
}

/* Forms */
[type="color"],
[type="date"],
[type="datetime"],
[type="datetime-local"],
[type="email"],
[type="month"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"],
[type="week"],
[type="time"],
select,
textarea {
  display: block;
  border: 1px solid #dedede;
  border-radius: 4px;
  padding: 0.75rem;
  outline: none;
  background: transparent;
  margin-bottom: 0.5rem;
  font-size: 1rem;
  width: 100%;
  max-width: 100%;
  line-height: 1;
}

[type="color"]:hover,
[type="date"]:hover,
[type="datetime"]:hover,
[type="datetime-local"]:hover,
[type="email"]:hover,
[type="month"]:hover,
[type="number"]:hover,
[type="password"]:hover,
[type="search"]:hover,
[type="tel"]:hover,
[type="text"]:hover,
[type="url"]:hover,
[type="week"]:hover,
[type="time"]:hover,
select:hover,
textarea:hover {
  border: 1px solid #c8c8c8;
}

[type="color"]:focus,
[type="color"]:active,
[type="date"]:focus,
[type="date"]:active,
[type="datetime"]:focus,
[type="datetime"]:active,
[type="datetime-local"]:focus,
[type="datetime-local"]:active,
[type="email"]:focus,
[type="email"]:active,
[type="month"]:focus,
[type="month"]:active,
[type="number"]:focus,
[type="number"]:active,
[type="password"]:focus,
[type="password"]:active,
[type="search"]:focus,
[type="search"]:active,
[type="tel"]:focus,
[type="tel"]:active,
[type="text"]:focus,
[type="text"]:active,
[type="url"]:focus,
[type="url"]:active,
[type="week"]:focus,
[type="week"]:active,
[type="time"]:focus,
[type="time"]:active,
select:focus,
select:active,
textarea:focus,
textarea:active {
  border: 1px solid #0366ee;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.1), 0 0 6px #87bfff;
}

textarea {
  overflow: auto;
  height: auto;
}

label {
  font-weight: 600;
  max-width: 100%;
  display: block;
  margin: 1rem 0 0.5rem;
}

/* Tables */
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  max-width: 100%;
}

thead th {
  border-bottom: 2px solid #CD5C5C;
  padding: 0.75rem;
}

th,
td {
  text-align: left;
  padding: 2rem;
} /* Desktop */

.striped-table tbody tr:nth-child(odd) {
  background-color: #fffff949;
}

.contain-table {
  overflow-x: auto;
}

@media (max-width: 768px) {
  header {
    margin-top: 30px !important;
  }
  
  .contain-table {
    margin-top: 15px;
  }

  th,
  td {
    padding: 2rem 0.25rem;
    font-size: 0.85rem;
  }
}

@media (min-width: 600px) {
  .contain-table {
    width: 100%;
  }
}

/* Text alignment */
.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

/* Occupancy fraction styling */
.occupancy-fraction {
  font-weight: 600;
  font-size: 1.1em;
  color: var(--gray-700);
  display: inline-block;
  padding: 0.25rem 0.5rem;
  background: var(--gray-100);
  border-radius: 4px;
  min-width: 2.5rem;
  text-align: center;
}
